<template>
  <div>
    <group title="single column">
      <popup-picker :title="title1" :data="list1" v-model="value1" @on-show="onShow" @on-hide="onHide"></popup-picker>
    </group>
    <br>
    <div class="picker-buttons">
      <x-button type="primary" @click.native="changeList10"></x-button>
      <x-button type="primary" @click.native="changeList11">push</x-button>
    </div>
    <group title="double columns">
      <popup-picker :title="title2" :data="list2" v-model="value2"></popup-picker>
    </group>
    <br>

    <group title="chained columns">
      <popup-picker :title="title3" :data="list3" :columns="3" v-model="value3" ref="picker3"></popup-picker>
      <cell title="" value="$refs.picker3.getNameValues()"></cell>
      <popup-picker :title="title4" :data="list3" :columns="3" v-model="value4" show-name></popup-picker>
    </group>

    <br>
    <div class="picker-buttons">
      <x-button type="primary" @click.native="changeList21">push</x-button>
    </div>

    <br>
    <divider>Control the visibility of popup-picker</divider>
    <div style="margin: 0 15px;">
      <x-button @click.native="showPopupPicker = true" type="primary">Show PopupPicker. value: {{value5 }}</x-button>
    </div>
    <group>
      <popup-picker :show="showPopupPicker" :show-cell="false" title="TEST" :data="[['1', '2', '3', '4', '5']]" v-model="value5" @on-hide="showPopupPicker=false"></popup-picker>
    </group>

    <br>
    <group title="popup-pickermask">
      <x-switch title="ishide popup-picker" v-model="switch6"></x-switch>
      <popup-picker v-if="!switch6" :show.sync="showPopupPicker" title="" :data="[''.split('')]" v-model="value6"></popup-picker>
    </group>

    <br>
    <br>

  </div>
</template>

<script>
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'


export default {
  components: {
    PopupPicker,
    Group,
    Picker,
    XButton,
    Divider,
    Cell,
    XSwitch
  },
  methods: {
    changeList10 () {
      this.list1 = [['1', 'iPhone1', '1', '1', '1', '1', '1']]
    },
    changeList11 () {
      this.list1[0].push('push')
    },
    changeList20 () {

    },
    changeList21 () {
      this.list3.push({
        name: '002_007',
        value: '0007',
        parent: 'usa002'
      })
    },
    onShow () {
      console.log('on show')
    },
    onHide (type) {
      console.log('on hide', type)
    }
  },
  data () {
    return {
      title1: '',
      title2: '',
      title3: '',
      title4: '',
      list1: [['', 'iPhone', '', '', '', '', '']],
      list2: [['', 'iPhone', '', '', '', '', ''], ['1', 'iPhone2', '3', '4', '5', '6', '7']],
      list3: [{
        name: '',
        value: 'china',
        parent: 0
      }, {
        name: '',
        value: 'USA',
        parent: 0
      }, {
        name: '',
        value: 'china001',
        parent: 'china'
      }, {
        name: '',
        value: 'china002',
        parent: 'china'
      }, {
        name: '001',
        value: 'usa001',
        parent: 'USA'
      }, {
        name: '002',
        value: 'usa002',
        parent: 'USA'
      }, {
        name: '',
        value: 'gz',
        parent: 'china001'
      }, {
        name: '',
        value: 'sz',
        parent: 'china001'
      }, {
        name: '001',
        value: 'gx001',
        parent: 'china002'
      }, {
        name: '002',
        value: 'gx002',
        parent: 'china002'
      }, {
        name: '001_001',
        value: '0003',
        parent: 'usa001'
      }, {
        name: '001_002',
        value: '0004',
        parent: 'usa001'
      }, {
        name: '002_001',
        value: '0005',
        parent: 'usa002'
      }, {
        name: '002_002',
        value: '0006',
        parent: 'usa002'
      }],
      value1: ['iPhone'],
      value2: ['iPhone', '3'],
      value3: [],
      value4: [],
      showPopupPicker: false,
      value5: ['2'],
      switch6: false,
      value6: []
    }
  }
}
</script>

<style scoped>
.picker-buttons {
  margin: 0 15px;
}
</style>




